/* MODULAR SCALE */
/* ------------
-- Variables --
------------ */
@import url(//cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
@import url(//cdnjs.cloudflare.com/ajax/libs/dropzone/5.1.1/min/dropzone.min.css);

.loading-button,
.loading-button .loading-spinner,
.loading-button .loading-label {
    transition: 0.3s ease all !important;
}

.loading-button {
    overflow: hidden;
}

.loading-button .loading-spinner {
    left: 50%;
    margin-left: 32px/-2;
    -webkit-transform: scale(0.2);
    transform: scale(0.2);
}

.loading-button .loading-label {
    position: relative;
    display: inline-block;
}

.loading-button .loading .loading-label {
    opacity: 0;
    -webkit-transform: scale(2.2);
    transform: scale(2.2);
}

.loading-button .loading .loading-spinner {
    opacity: 1;
    margin-left: 0;
    -webkit-transform: none;
    transform: none;
}

:root {
    font-size: calc(1rem + calc(22 - 16) * calc(calc(100vw - 320px) / calc(3840 - 320)));
    line-height: 1.38;
}

/* -----------------
  -- Align Browsers --
  ----------------- */
::before,
::after {
    text-decoration: inherit;
    vertical-align: inherit;
}

/* Add the correct display in IE 9-. */
article,
aside,
footer,
header,
nav,
section,
details,
menu,
summary,
canvas,
video {
    display: block;
}

/**
    * Prevent modern browsers from displaying `audio` without controls.
    * Remove excess height in iOS 5 devices.
    */
audio:not([controls]) {
    display: none;
    height: 0;
}

/* Hide elements that should be hidden in IE, Safari, and Firefox < 22. */
[hidden],
template,
script,
style {
    display: none;
}

img {
    /* prevent dragging of ghost image */
    -webkit-user-drag: none;
    /* Responsive images! */
    max-width: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    -o-object-position: center center;
    object-position: center center;
}

/* tables still need cellspacing="0" in the markup */
table {
    border-collapse: separate;
    border-spacing: 0;
}

/* --------------------------------------
  -- User Selection Based On Device Type --
  -------------------------------------- */
/* One or more available input mechanism(s) 
     can hover over elements with ease */
@media (any-hover: hover) {
    html {
        -webkit-user-select: text;
        -moz-user-select: text;
        -ms-user-select: text;
        user-select: text;
    }
}

/* One or more available input mechanism(s) can hover, 
        but not easily (e.g., many mobile devices emulate 
        hovering when the user performs a long tap) */
@media (any-hover: on-demand) {
    html {
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
}

/* One or more available input mechanism(s) cannot 
        hover (or there are no pointing input mechanisms) */
@media (any-hover: none) {
    html {
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
}

/* -------
  -- ARIA --
  ------- */
/* Change the cursor on busy elements (opinionated). */
[aria-busy=true] {
    cursor: progress;
}

/* Change the cursor on control elements (opinionated). */
[aria-controls] {
    cursor: pointer;
}

/* Change the display on visually hidden accessible elements (opinionated). */
[aria-hidden=false][hidden]:not(:focus) {
    clip: rect(0, 0, 0, 0);
    display: inherit;
    position: absolute;
}

/* Change the cursor on disabled, not-editable, or otherwise inoperable elements (opinionated). */
[aria-disabled] {
    cursor: default;
}

