/* Restyle Range Slider */
[type=range] {
    -webkit-appearance: none;
    margin: 1rem 0;
    width: 100%;
}

[type=range]::-moz-focus-outer {
    border: 0;
}

[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 0.25rem;
    padding: 0.125rem;
    cursor: pointer;
    animate: 0.2s;
    box-shadow: none;
    background: rgba(0, 32, 51, 0.125);
    border-radius: 25px;
    border: none;
}

[type=range]::-webkit-slider-thumb {
    box-shadow: none;
    border: none;
    height: 1rem;
    width: 1rem;
    border-radius: 99999px;
    background: #007ac3;
    cursor: pointer;
    -webkit-appearance: none;
    -webkit-transform: translate3d(0, -0.5rem, 0) scale3d(1, 1, 1);
    transform: translate3d(0, -0.5rem, 0) scale3d(1, 1, 1);
    border: 0.0625rem solid rgba(255, 255, 255, 1);
    transition-property: border-color, background-color, -webkit-transform;
    transition-property: border-color, transform, background-color;
    transition-property: border-color, transform, background-color, -webkit-transform;
}

[type=range]::-webkit-slider-thumb:hover,
[type=range]:focus::-webkit-slider-thumb,
[type=range]:active::-webkit-slider-thumb {
    -webkit-transform: translate3d(0, -0.5rem, 0) scale3d(1.2, 1.2, 1.2);
    transform: translate3d(0, -0.5rem, 0) scale3d(1.2, 1.2, 1.2);
    border: 0.125rem solid #007ac3;
    background: rgba(255, 255, 255, 1);
    transition-property: border-color, background-color, -webkit-transform;
    transition-property: border-color, transform, background-color;
    transition-property: border-color, transform, background-color, -webkit-transform;
}

[type=range]::-moz-range-track {
    width: 100%;
    height: 0.25rem;
    cursor: pointer;
    animate: 0.2s;
    box-shadow: none;
    background: rgba(0, 32, 51, 0.125);
    border-radius: 25px;
    border: none;
    transform: scale3d(1, 1, 1);
}

[type=range]::-moz-range-thumb {
    box-shadow: none;
    border: 0.0625rem solid rgba(255, 255, 255, 1);
    height: 1rem;
    width: 1rem;
    border-radius: 99999px;
    background: #007ac3;
    cursor: pointer;
    transform: scale3d(1, 1, 1);
    border: 0.0625rem solid rgba(255, 255, 255, 1);
    transition-property: border-color, background-color, -webkit-transform;
    transition-property: border-color, transform, background-color;
    transition-property: border-color, transform, background-color, -webkit-transform;
}

[type=range]::-moz-range-thumb:hover,
[type=range]:focus::-moz-range-thumb,
[type=range]:active::-moz-range-thumb {
    transform: scale3d(1.2, 1.2, 1.2);
    border: 0.125rem solid #007ac3;
    background: rgba(255, 255, 255, 1);
    transition-property: border-color, background-color, -webkit-transform;
    transition-property: border-color, transform, background-color;
    transition-property: border-color, transform, background-color, -webkit-transform;
}

[type=range]::-ms-track {
    width: 100%;
    height: 0.25rem;
    padding: 0.125rem;
    cursor: pointer;
    animate: 0.2s;
    background: transparent;
    border-color: transparent;
    border-width: 39px 0;
    color: transparent;
}

[type=range]::-ms-fill-lower {
    background: rgba(0, 32, 51, 0.125);
    border: none;
    border-radius: 50px;
    box-shadow: none;
}

[type=range]::-ms-fill-upper {
    background: rgba(0, 32, 51, 0.125);
    border: none;
    border-radius: 50px;
    box-shadow: none;
}

[type=range]::-ms-thumb {
    box-shadow: none;
    border: 0.0625rem solid rgba(255, 255, 255, 1);
    height: 1rem;
    width: 1rem;
    border-radius: 99999px;
    background: #007ac3;
    cursor: pointer;
    transform: scale3d(1, 1, 1);
    border: 0.0625rem solid rgba(255, 255, 255, 1);
    transition-property: border-color, background-color, -webkit-transform;
    transition-property: border-color, transform, background-color;
    transition-property: border-color, transform, background-color, -webkit-transform;
}

[type=range]::-ms-thumb:hover,
[type=range]:active::-ms-thumb {
    transform: scale3d(1.2, 1.2, 1.2);
    border: 0.125rem solid #007ac3;
    background: rgba(255, 255, 255, 1);
    transition-property: border-color, background-color, -webkit-transform;
    transition-property: border-color, transform, background-color;
    transition-property: border-color, transform, background-color, -webkit-transform;
}

