}

/* Checkbox, Radio, File */
[type=checkbox] {
    border-radius: 0.125rem;
}

[type=radio] {
    border-radius: 100%;
}

[type=checkbox],
[type=radio] {
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    /* in case this ever gets supported */
    background: rgba(255, 255, 255, 1);
    border: 0.125rem solid transparent;
    box-shadow: 0 0 0 0.125rem rgba(0, 0, 0, 0.125), #007ac3 inset 0 0 0 0;
    box-shadow: 0 0 0 0.125rem rgba(0, 0, 0, 0.125), inset 0 0 0 0 #007ac3;

    display: inline-block;
    height: 1em;
    margin: 0.25em;
    transition-property: border-color, box-shadow;
    vertical-align: -38%;
    width: 1em;
}

[type=checkbox]:focus,
[type=radio]:focus {
    border-color: transparent;
    box-shadow: 0 0 0 0.125rem #007ac3, #007ac3 inset 0 0 0 0;
    box-shadow: 0 0 0 0.125rem #007ac3, inset 0 0 0 0 #007ac3;

}

[type=checkbox]:checked,
[type=radio]:checked {
    border-color: rgba(255, 255, 255, 1);
    box-shadow: 0 0 0 0.125rem rgba(0, 0, 0, 0.125), #007ac3 inset 0 0 0 0.5rem;
    box-shadow: 0 0 0 0.125rem rgba(0, 0, 0, 0.125), inset 0 0 0 0.5rem #007ac3;

}

[type=checkbox]:checked:focus,
[type=radio]:checked:focus {
    box-shadow: 0 0 0 0.125rem #007ac3, #007ac3 inset 0 0 0 0.5rem;
    box-shadow: 0 0 0 0.125rem #007ac3, inset 0 0 0 0.5rem #007ac3;

}

[type=checkbox][disabled],
[type=checkbox][disabled]+span,
[type=radio][disabled],
[type=radio][disabled]+span {
    opacity: 0.4;
}

/* STATE */
input:focus:invalid,
.input-wrapper--invalid,
textarea:focus:invalid,
select:focus:invalid {
    border-color: #ea1c0d;
    color: #f44336;
}

/* SELECT */
select:not([size]):not([multiple]) {
    height: -webkit-(2.375rem);
    height: 2.375rem;
}

select[multiple] {
    height: auto;
}

select:-moz-focusring {
    color: transparent;
    text-shadow: 0 0 0 rgba(0, 0, 0, 1);
}

option:not(:checked) {
    color: #002033;
    /* prevent <option>s from becoming transparent as well */
}

/* Buttons */
[type=submit],
[type=reset],
[type=button],
button,
.button {
    border: 0;
    text-decoration: none;
    border-radius: 0.25rem;
    padding: 0.5rem 1rem;
    margin: 0;
    display: inline-block;
    font-weight: inherit;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    line-height: 1.5;
    position: relative;
    /* Interaction */
    -webkit-user-drag: none;
    -webkit-tap-highlight-color: transparent;
    -webkit-tap-highlight-color: transparent;
    -moz-osx-font-smoothing: grayscale;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    /* Animation */
    transition-property: border-color, background-color, box-shadow;
}

/* Details & Summary  */
summary::-webkit-details-marker {
    display: none;
}

/* Progress Bar */
progress {
    width: 100%;
    /* Firefox */
    border: none;
    background: rgba(0, 32, 51, 0.025);
    border: 0 solid transparent;
    border-radius: 99999px;
}

progress::-webkit-progress-bar {
    background: rgba(0, 32, 51, 0.025);
    border: 0 solid transparent;
    border-radius: 99999px;
}

progress::-webkit-progress-value {
    background: #007ac3;
    border-radius: 99999px;
}

progress::-moz-progress-bar {
    background: #007ac3;
    border-radius: 99999px;
}

/* Meter */
meter {
    background: rgba(0, 32, 51, 0.025);
    border: 0 solid transparent;
    border-radius: 0;
    color: #007ac3;
    width: 100%;
    outline: 0;
    overflow: hidden;
}

meter::-moz-meter-bar {
    background: none;
    background-color: #007ac3;
}

meter::-webkit-meter-bar {
    background: none;
    background-color: #007ac3;
}

/* Details & Summary */
details>summary:first-of-type {
    list-style-type: none;
}

summary::-webkit-details-marker {
    display: none;
