}

/* only mobile */
@media (max-width: 850px) {
    app-root> :nth-child(n+3) {
        flex: 1 1 auto;
        min-width: 0;
        min-height: 0;
        display: flex;
        overflow: auto;
    }

    /* Fixes auto-zoom. Pixel value must be hardcoded or else it doesn't work. */
    input[type=text],
    textarea {
        font-size: 18px;
        line-height: 20px;
    }
}

@media (min-width: 850px) {
    .overflow-visible {
        overflow: visible !important;
        -ms-flex-item-align: normal !important;
        -ms-grid-row-align: normal !important;
    }
}

app-root> :nth-child(n+3) {
    -ms-grid-row-align: stretch;
    align-self: stretch;
    overflow: auto;
}

